<script>
import toursure from '@/translations/toursure.json'
import Claims from '../components/Claims'

export default {
  name: 'toursure',
  components: {Claims},
  i18n: {
    messages: toursure
  },
  mounted () {
    let defaultLocale = window.localStorage.getItem('language')

    if (defaultLocale) {
      this.$i18n.locale = JSON.parse(defaultLocale).locale
    }
  },
  created () {
    this.$eventHub.$on('lang-change', (lang) => {
      if (this.$i18n) {
        this.$i18n.locale = lang
      }
    })
  }
}
</script>

