<script>

import history from '@/translations/history'

export default {
  name: 'history',
  i18n: {
    messages: history
  },
  mounted () {
    let defaultLocale = window.localStorage.getItem('language')

    if (defaultLocale) {
      this.$i18n.locale = JSON.parse(defaultLocale).locale
    }
  },
  created () {
    this.$eventHub.$on('lang-change', (lang) => {
      if (this.$i18n) {
        this.$i18n.locale = lang
      }
    })
  }
}
