<script>
export default {
  name: 'GPopUpImage',
  data () {
    return {
      file: {
        url: './images/popup.jpg'
      }
    }
  },
  mounted () {
    this.setUp()
  },
  methods: {
    setUp () {
      window.$(document).ready(function () {
        window.$('.lightbox').each(function () {
          window.$(this).attr('href', window.$(this).attr('data-href'))
          window.$(this).initMagnificPopup()
          window.$(this).click()
        })

        // setTimeout(function () {
        //   window.$('.lightbox').magnificPopup('close')
        // }, 10000)
      })
    }
  }
}
